import configdata from './config'
import cache from './cache'
import lbframe from './lbframe'

module.exports = {
	config: function(name) {
		var info = null;
		if (name) {
			var name2 = name.split("."); //
			if (name2.length > 1) {
				info = configdata[name2[0]][name2[1]] || null;
			} else {
				info = configdata[name] || null;
			}
			if (info == null) {
				let web_config = cache.get("web_config");
				if (web_config) {
					if (name2.length > 1) {
						info = web_config[name2[0]][name2[1]] || null;
					} else {
						info = web_config[name] || null;
					}
				}
			}
		}
		return info;
	},
	post: function(url, data, header) {
		var Sign = lbframe.getSign(data);
		var userToken = lbframe.getUserInfo();
		if (userToken == undefined || userToken == '') {
			var token = '';
		} else {
			var token = userToken;
		}
		url = this.config("APIHOST") + url;
		if (!header) header = {"content-type": "application/x-www-form-urlencoded"};
		return new Promise((resolve, reject) => {
			uni.request({
				url: url,
				data: data,
				method: 'POST',
				header: {
					...header,
					// "content-type": "application/json; charset=utf-8",
					"USER-TOKEN": token,
					"appid": Sign.appid,
					"signaturenonce": Sign.signaturenonce,
					"signature": Sign.signature,
					"timestamp": Sign.timestamp
				},
				success: function(res) {
					if (res.statusCode == 200) {
						if (res.data.code == -201) {
							// console.log('');
							//
							uni.showToast({
								title: res.data.msg,
								icon: 'none'
							});
							uni.removeStorageSync('USERINFO')
							if (uni.getStorageSync('istoken')) {
								uni.showToast({
									title: res.data.msg,
									icon: 'none'
								});
								setTimeout(() => {
									uni.setStorageSync('istoken', true);
								}, 30000)
								setTimeout(function() {







									uni.navigateTo({ // redirectTo
										url: '/pagessignup/wechat/wxlog'
									});

								}, 500)
								uni.setStorageSync('istoken', false);
								return false;
							}

							return false;
						}
						resolve(res.data);
					} else {
						uni.showToast({
							title: res.errMsg,
							icon: 'none'
						});
						reject();
					}
				},
				fail(e) {
					reject(e);
				}
			});
		})
	},
	get: function(url, data) {
		var Sign = lbframe.getSign(data);
		var userToken = lbframe.getUserInfo();
		if (userToken == undefined || userToken == '') {
			var token = '';
		} else {
			var token = userToken;
		}
		url = this.config("APIHOST") + url;
		return new Promise((resolve, reject) => {
			uni.request({
				url: url,
				data: data,
				method: 'GET',
				header: {
					//"content-type": "application/x-www-form-urlencoded",
					"USER-TOKEN": token,
					"appid": Sign.appid,
					"signaturenonce": Sign.signaturenonce,
					"signature": Sign.signature,
					"timestamp": Sign.timestamp
				},
				success: function(res) {
					if (res.statusCode == 200) {
						if (res.data.code == -201) {
							//
							uni.showToast({
								title: res.data.msg,
								icon: 'none'
							});
							uni.removeStorageSync('USERINFO')

							if (uni.getStorageSync('istoken')) {
								uni.showToast({
									title: res.data.msg,
									icon: 'none'
								});
								setTimeout(() => {
									uni.setStorageSync('istoken', true);
								}, 30000)
								setTimeout(function() {







									uni.navigateTo({ // redirectTo
										url: '/pagessignup/wechat/wxlog'
									});

								}, 500)
								uni.setStorageSync('istoken', false);
								return false;
							}
							return false;
						}
						resolve(res.data);
					} else {
						uni.showToast({
							title: res.errMsg,
							icon: 'none'
						});
						reject();
					}
				}
			});
		})
	},
	/*
	 *   
	 * 		successFn  //
	 * 		num		  	//
	 * 		data		//
	 * 		progressFn	//
	 */

	uploadImg(successFn, num = 1, data = {}, progressFn) { //
		let url = this.config("APIHOST");
		uni.chooseImage({
			count: num, //1
			sizeType: ['original', 'compressed'], //
			sourceType: ['album', 'camera'], //
			success: function(res) { //
				uni.showLoading({
					title: '...'
				})
				// console.log(res, '========res');
				const tempFilePaths = res.tempFilePaths;
				let fileList = {}; //
				for (var i in tempFilePaths) {
					fileList['uploadTask' + i] = uni.uploadFile({ //
						url: url + 'v1/5d5fa8984f0c2',
						filePath: tempFilePaths[i],
						name: 'file[]',
						formData: data,
						success: function(upRes) { //
							uni.hideLoading();
							// console.log(upRes, '========upRes');
							if (upRes.statusCode == 200) {
								let data = JSON.parse(upRes.data);
								if (data.code == 0) {
									uni.showToast({
										title: '',
										icon: 'none'
									})
									return
								}
								if (typeof(successFn) == 'function') {
									successFn(data)
								}
							} else {
								uni.showToast({
									title: upRes.errMsg,
									icon: 'none'
								})
							}
						},
						fail: function(err) {
							uni.hideLoading();
							uni.showToast({
								title: '' ,
								icon: 'none'
							})
						}
					})
					//
					fileList['uploadTask' + i].onProgressUpdate(function(res) {
						if (typeof(progressFn) == 'function') {
							progressFn(res)
						}
						if (res.progress < 100) {
							// uni.showLoading({
							// 	title: res.progress + '%'
							// });
						} else {
							// uni.hideLoading()
						}
					});
				};
			}, //end
			fail(err) {
				// console.log('err:', err);
			}
		});
	}
}
