let ageLimit = setAge([1,10]);	//
ageLimit.unshift({
	label: '',
	value: ''
})
ageLimit.push({
	label: '10',
	value: '10'
})

function setAge(data){
	var arr = [];
	for(var i=data[0];i<=data[1];i++){
		var obj = {
			label: i,
			value: i,
			children:[]
		}
		for(var j=i;j<=data[1];j++){
			obj.children.push({
				label: j,
				value: j,
			})
		}
		arr.push(obj)
	}
	return arr
}



