export function checkUpdater(currentId, updaterPage) {
	uni.request({
			url: 'https://app.chongdarenli.com/api/v1/5d67b2acdd34d',
			method: 'POST',
			success: res => {
				console.log('-----------------', res);
				if (res.data.code == 1) {
					const response = res.data.data
					const latestId = response.version
					const method = response.force
					if (currentId < latestId && method == 1) {
						const iosLink = response.iosLink
						const androidLink = response.is_update_apk
						let downloadLink = ''
						let ready = false
						// 
						if (plus.os.name.toLowerCase() === 'android') {
							console.log('')
							if (androidLink && androidLink !== '#') { // #
								console.log('')
								// 
								if (androidLink.match(RegExp(/.wgt/))) {
									console.log('wgt')
									downloadLink = androidLink
									ready = true
								} else {
									ready = true
									downloadLink = androidLink
									console.log(androidLink + '.wgt.apk')
									// uni.showModal({
									//     title: '',
									//     content: 'app',
									//     success: function (res) {
									//         if (res.confirm) {
									//             console.log('');
									// 			plus.runtime.openURL(androidLink); //  
									//         } else if (res.cancel) {
									//             console.log('');
									//         }
									//     }
									// });
								}
							} else {
								console.log('')
							}
						} else {
							if (iosLink && iosLink !== '#') { // #
								// (A)iosLinkwgt.wgt
									if (iosLink.match(RegExp(/.wgt/))) {
										downloadLink = iosLink
										ready = true
									} else {
										// console.log('.wgt')
										plus.runtime.openURL(iosLink); //  
									}
								}
								else {
									console.log('')
								}
							}
							if (ready) {
								// console.log('');
								uni.showToast({
									title: '...',
									icon: 'none',
									duration: 2000
								});
								let downloadTask = uni.downloadFile({
									url: downloadLink,
									success: (res) => {
										if (res.statusCode === 200) {
											// 
											console.log('')
											uni.saveFile({
												tempFilePath: res.tempFilePath,
												success: (res) => {
													const packgePath = res.savedFilePath
													// stroageapp
													uni.setStorage({
														key: 'updated',
														data: {
															completed: false,
															packgePath: packgePath
														},
														success: () => {
															console.log('')
														}
													})
													// 
													console.log('')
													// 
													plus.runtime.install(packgePath, {
														force: true
													});
													// stroageapp
													uni.setStorage({
														key: 'updated',
														data: {
															completed: true,
															packgePath: packgePath
														},
														success: res => {
															console.log('');
														}
													});
													// .wgt
													if (packgePath.match(RegExp(/.wgt/))) {
														uni.showModal({
															title: '',
															content: '',
															showCancel: false,
															complete: () => {
																plus.runtime.restart();
															}
														});
													}
													/* downloadTask.abort()
													 downloadTask = null */
												}
											})
										}
									}
								})
							} else {
								console.log('')
							}

						} else if (currentId < latestId) {
							console.log('')
							uni.showModal({
								title: '',
								content: ' (id:' + latestId + ')',
								success: (res) => {
									if (res.confirm) {
										uni.navigateTo({
											url: updaterPage
										})
									} else if (res.cancel) {
										console.log('')
									}
								}
							})
						} else {
							console.log('')
						}
					}
				}
			})
	}
